export default {
  props: ["data"],
  data: function data() {
    return {
      account: "",
      imageUrl: ""
    };
  },
  created: function created() {},
  mounted: function mounted() {},
  methods: {
    open: function open() {
      this.account = "";
      this.imageUrl = "";
    },
    submitUnderline: function submitUnderline() {
      if (!this.account) {
        this.$elementMessage('Pay account must be filled in', 'error');
        return;
      }

      if (!this.imageUrl) {
        this.$elementMessage('Pay voucher must be filled in', 'error');
        return;
      }

      this.$emit("submitUnderline", this.account, this.imageUrl);
    },
    uploadSectionFile2: function uploadSectionFile2(params) {
      var _this = this;

      var uploadFile = params.file; // const isLt = uploadFile.size / 1024 / 1024 < 0.5;
      // if (!isLt) {
      //   this.$message.error("上传图片大小不能超过 500kb!");
      //   return;
      // }

      this.$showLoading();
      this.$apiCall("api.User.uploadImg", {
        "@file": uploadFile
      }, function (r) {
        if (r.ErrorCode == "9999") {
          _this.imageUrl = r.Data.Results.imgUrl;
        } else {
          _this.$elementMessage(r.Message, "error");
        }

        _this.$hideLoading();
      });
    }
  }
};